"use client";

import React, { useEffect, useState } from "react";
import "@/styles/SellersScreen.scss";
import { useSession } from "next-auth/react";
import { fetchData } from "../../utils/api";

const SellersScreen = ({ sucursalId, collaborator }) => {
  const { data: session } = useSession();
  const [colaboradores, setColaboradores] = useState([]);

  useEffect(() => {
    async function fetchCollaborators() {
      if (!session?.user) return;

      try {
        const res = await fetchData(
          `/users/${session.user.id}/sucursales/${sucursalId}/children`,
          "GET",
          null
        );

        if (res.length === 0) {
          console.log("No se encontraron colaboradores.");
        }
        if (res.error) console.error("Error:", res.error);

        setColaboradores(res);
      } catch (error) {
        console.error("Error cargando las sucursales:", error);
      }
    }

    fetchCollaborators();
  }, [session]);

  function handleRowClick(row) {
    if (collaborator) {
      const cardObject = {
        id: row?.user_id,
        name: row?.userName,
        role: row?.role,
      };
      collaborator(cardObject);
    } else {
      console.error("handleRoute no est√° definido");
    }
  }

  return (
    <div className="sellers-screen">
      <section className="section1">
        <div className="MenuProjectSection">
          <div className="infoContainer">
            <h2>Total</h2>
            <div className="infoBox">
              <div className="infoRow">
                <div>Compras</div>
                <div>Ventas</div>
                <div>Compras</div>
                <div>Liquidado</div>
              </div>
              <div className="valueRow">
                <div>$0</div>
                <div>$0</div>
                <div>$0</div>
                <div>$0</div>
              </div>
            </div>
          </div>
          <div className="codingContainer">
            <h2>
            A√±adir Colaborador
            </h2>
            <div className="codingBox">

            </div>
          </div>
        </div>
      </section>
      <section className="section2">
        <div className="table-container">
          <h2>Colaboradores</h2>
          <table className="collaborators-table">
            <thead>
              <tr>
                <th>#</th>
                <th>Nombre</th>
                <th>Tipo</th>
                <th>Compras/Ventas ($)</th>
                <th>Ubicaci√≥n</th>
                <th>Tel√©fono</th>
              </tr>
            </thead>
            <tbody>
              {colaboradores.map((colaborador, index) => (
                <tr key={index} onClick={() => handleRowClick(colaborador)}>
                  <td>{index + 1}</td>
                  <td>{colaborador.userName}</td>
                  <td>{colaborador.role}</td>
                  <td>{colaborador.bought_sold}</td>
                  <td>
                    <a href="#">Ver</a>
                  </td>
                  <td>{colaborador.phone}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </section>
    </div>
  );
};

export default SellersScreen;
