"use client";

import React, { useEffect, useState } from "react";
import "@/styles/InvoicesSellerScreen.scss";
import { useSession } from "next-auth/react";
import { fetchData } from "../../utils/api";

const InvoicesSellerScreen = ({ collaboratorId, invoice }) => {
  const { data: session } = useSession();
  const [invoices, setInvoices] = useState([]);

  useEffect(() => {
    async function fetchInvoices() {
      if (!session?.user || !collaboratorId?.id) return;

      try {
        const res = await fetchData(
          `/users/${session.user.id}/collaborators/${collaboratorId.id}/invoices`,
          "GET",
          null
        );

        console.log("Res:", res);

        if (res.length === 0) {
          console.log("No se encontraron facturas.");
        }
        if (res.error) {
          console.error("Error:", res.error);
        }

        res.forEach((element) => {
          element.created_at = new Date(element.created_at).toLocaleDateString(
            "es-ES",
            {
              day: "2-digit",
              month: "2-digit",
              year: "numeric",
              timeZone: "America/Bogota",
            }
          );
        });

        setInvoices(res);
      } catch (error) {
        console.error("Error cargando las facturas:", error);
      }
    }

    fetchInvoices();
  }, [session, collaboratorId]);

  const handleRowClick = (row) => {

    if (invoice) {
      invoice(row);
    } else {
      console.error("handleRoute no est√° definido");
    }
  }

  return (
    <div className="table-container">
      <h2>Facturas del vendedor: {collaboratorId?.name}</h2>
      <table className="collaborators-table">
        <thead>
          <tr>
            <th>#</th>
            <th>Fecha</th>
            <th>C√≥digo de Cliente</th>
            <th>Valor Neto</th>
            <th>Pago</th>
            <th>Fecha de Entrega</th>
            <th>Liquidado</th>
          </tr>
        </thead>
        <tbody>
          {invoices.map((invoice, index) => (
            <tr key={index} onClick={() => handleRowClick(invoice)}>
              <td>{index + 1}</td>
              <td>{invoice.created_at}</td>
              <td>{invoice.user_buyer_id}</td>
              <td>${invoice.total_net}</td>
              <td>{invoice.state == "pending" ? "N" : "S"}</td>
              <td>
                {invoice.deliveryDate == null ? "--" : invoice.deliveryDate}
              </td>
              <td>{invoice.sold_out == "pending" ? "N" : "S"}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default InvoicesSellerScreen;
